import React from "react"
import styled from "styled-components"
import QF from "../components/quickfind"

const search = new QF(100)

var Box = styled.div`
  height: 40px;
  width: 40px;
  box-sizing: border-box;
  border: 1px solid black;
  background-color: ${props => props.color};
  @media (max-width: 820px) {
    height: 30px;
    width: 30px;
    margin-left: 1em;
    margin-right: 1em;
  }
`
const boardConstruct = function(n) {
  var result = []
  for (var i = 0; i < n; i++) {
    var random = Math.floor(Math.random() * Math.floor(10))
    result.push(<Box color={random < 6 ? "white" : "black"} />)
  }
  return result
}

class quicksearch extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      newBoard: boardConstruct(100),
    }
    this.percolate = this.percolate.bind(this)
  }

  percolate() {
    this.state.newBoard.map((box, index) => {
      if (box.props.color === "white") {
        if (
          index % 10 !== 0 &&
          this.state.newBoard[index - 1].props.color !== "black"
        ) {
          search.union(index, index - 1)
        }
        if (
          index > 9 &&
          this.state.newBoard[index - 10].props.color !== "black"
        ) {
          search.union(index, index - 10)
        }
      }
    })
    this.state.newBoard.map((box, index) => {
      if (search.root(index) < 10) {
        this.setState({ [this.state.newBoard[index].props.color]: "blue" })
      }
    })

    // setTimeout(() => {
    //   this.forceUpdate()
    // }, 500)
  }

  render() {
    const Section = styled.div`
      display: flex;
      flex-wrap: wrap;
      height: 400px;
      width: 400px;
      margin: auto;
      padding-top: 10px;
      @media (max-width: 820px) {
        height: 300px;
        width: 300px;
        margin-left: 1em;
        margin-right: 1em;
      }
    `

    return (
      <Section>
        {this.state.newBoard}
        <button onClick={this.percolate}>percolate</button>
      </Section>
    )
  }
}

export default quicksearch
